        <div class="column large-6">
          <input
            type="text"
            class="field"
            placeholder="Enter Your Last Name"
            required
          />
        </div>
        <div class="column large-6">
          <input
            type="tel"
            class="field"
            placeholder="Enter Your Phone"
            required
          />
        </div>
        <div class="column large-6">
          <input
            type="email"
            class="field"
            placeholder="Enter Your Email"
            required
          />
        </div>
        <div class="column large-12">
          <input type="date" class="field" required />
        </div>
        <div class="column large-12">
          <textarea
            rows="1"
            placeholder="Enter Avalible Hours That Fits You"
          ></textarea>
        </div>
        <div class="groupcheck">
          <input type="checkbox" id="CUT" class="chkbx" />
          <label for="CUT">CUT</label>

          <input type="checkbox" id="BUZZ" class="chkbx" />
          <label for="BUZZ">BUZZ</label>

          <input type="checkbox" id="SHAVE" class="chkbx" />
          <label for="SHAVE">SHAVE</label>

          <input type="checkbox" id="TRIM" class="chkbx" />
          <label for="TRIM">BEARD TRIM</label>

          <input type="checkbox" id="ALL" class="chkbx" />
          <label for="ALL">CUT + SHAVE</label>
        </div>
        <p class="checkedWarning alert label">
          Please select at least one checkbox
        </p>
        <p>* We Will send you an e-mail with further details.</p>
        <button type="submit" class="button secondary submit">Book</button>
        <button type="reset" class="button secondary">Reset</button>
