<script>
import jquery from "jquery";
window.$ = jquery;
</script>

<script>
export default {
  name: "ScheduleModal",
  mounted() {
    $(".checkedWarning").hide();
    $(".myform").submit(function(e) {
      if (
        $("#CUT").is(":checked") ||
        $("#BUZZ").is(":checked") ||
        $("#SHAVE").is(":checked") ||
        $("#TRIM").is(":checked") ||
        $("#ALL").is(":checked")
      ) {
        $(".checkedWarning").hide();
        return true;
      } else {
        $(".checkedWarning").show();
        e.preventDefault();
        return false;
      }
    });
  }
};
</script>

