<script>
import ScheduleModal from "../components/ScheduleModal";
export default {
  name: "Navbar",
  components: {
    ScheduleModal
  },
  methods: {},
  mounted() {
    if ($(window).width() > 640) {
      $("nav .ul-background ul").show();
    }
    $(".dropdown-button button").click(function() {
      $("html, body")
        .stop()
        .animate({ scrollTop: 0 }, 500, "swing");
      $("nav .ul-background").fadeToggle(500);
      $(".ul-background").toggleClass("DisplayToggle");
      $(".grid-container").toggleClass("FullDisplay");
      $(".grid-container h2").toggleClass("H2DisplayToggle");
      $("body").toggleClass("OverFlow");
      $("button").toggleClass("Bars");
    });
    $(".ul-background ul li a").click(function() {
      $("nav .ul-background").fadeOut(500);
      $(".ul-background").removeClass("DisplayToggle");
      $(".grid-container").removeClass("FullDisplay");
      $(".grid-container h2").removeClass("H2DisplayToggle", 0);
      $("body").removeClass("OverFlow");
      $("button").removeClass("Bars");
    });
    $(".modal").hide();
    $(".cancel").click(function() {
      $(".modal").hide(300);
      $(".modalBreacker").css(
        {
          display: "none"
        },
        300
      );
      $("body").css({
        overflow: "auto"
      });
    });
    $(".modalTrigger").click(function() {
      $("html, body")
        .stop()
        .animate({ scrollTop: 0 }, 500, "swing");
      $(".modal").show(300);
      $("body").css({
        overflow: "hidden"
      });
      $(".modalBreacker").css(
        {
          display: "block"
        },
        3200
      );
    });
  }
};
</script>

